@import url(https://fonts.googleapis.com/css2?family=Lato:wght@100;300;400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Lato','Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  background-color: #fafbfc;
}

.logo {
  font-size: 40px;
  font-weight: bold;
}

.btn-nav {
  font-size: 90%;
  color: #7b68ee;
  width: 6em;
  background-color: transparent;
  border-color: #7b68ee;
  border-radius: .5em;
  border-width: .2em;
}

.main {
  height: 100vh;
  background-color: #fafbfc;
  background: linear-gradient(to bottom, #fafbfc 0%, #fafbfc 50%, #f0f0fc 50%, #f0f0fc  100%);
}

.nav-main {
  background-color: #fafbfc;
}

.btn-submit {
  background-color: #7b68ee;
  width: 100%;
  font-size: 20px;
}

.inputbar {
  -webkit-appearance: none;
  border: none;
  border-radius: 5px;
  background: #fafbfc;
  box-shadow: 7px 7px 14px #cfcfcc, -7px -7px 14px #ffffff;
}

.form-label {
  font-size: 25px;
  font-weight: 250px;
}

.form-check {
  -webkit-transform: scale(1.3);
  transform: scale(1.3);
}
.banner {
  font-size: 60px;
  font-weight: bold;
  margin-top: 8vh;
}

.subtitle {
  font-size: 30px;
  font-weight: 300;
  color: grey;
}

.avatar {
  margin-top: 10vh;
  width: 15vw;
}
a {
  font-size: 20px;
  font-weight: 300;
  color: #7b68ee;
}

.login-form {
  margin-top: 50px;
}

.banner {
  font-size: 60px;
  font-weight: bold;
  margin-bottom: 5vh;
}
a {
  font-size: 20px;
  font-weight: 300;
  color: #7b68ee;
}

.register-form {
  margin-top: 50px;
}

.banner {
  font-size: 60px;
  font-weight: bold;
  margin-bottom: 5vh;
}


.sidebar {
  position: fixed;
  background-color: #fafbfc;
  top: 100px;
  bottom: 0;
  left: 0;
  min-height: 100vh !important;
  z-index: 100;
  padding: 48px 0 0;
  box-shadow: inset -1px 0 0 rgba(0, 0, 0, .1);
}

#sidebar-wrapper{
  min-height: 100vh !important;
  width: 100vw;
  margin-left: -1rem;
  transition: margin .25s ease-out;
}

#sidebar-wrapper .sidebar-heading {
  padding: 0.875rem 1.25rem;
  font-size: 1.2rem;
}

#page-content-wrapper {
  min-width: 0;
  width: 100%;
}
.circle-tutor {
  display: flex;
  background-color: #f0f0fc;
  border-radius: 50%;
  margin-top: 10vh;
  width: 15vw;
  height: 15vw;
}

.circle-student {
  display: flex;
  background-color: #7b68ee;
  border-radius: 50%;
  margin-top: 10vh;
  width: 15vw;
  height: 15vw;
}

.circle-parent {
  display: flex;
  background-color: #7b94f7;
  border-radius: 50%;
  margin-top: 10vh;
  width: 15vw;
  height: 15vw;
}

.sub {
  height: 100vh;
  background-color: #fafbfc;
}
